.news-item {
    position: relative;
    display: block;
    width: 100%;
    max-width: 820px;
    padding-bottom: 85.36%;
    margin-bottom: 40px;
    color: $black;
    &:hover {
        .news-item {
            &__img {
                transform: scale(1.1); }
            &__title {
                color: $activeColor; }
            &__date {
                color: $activeColor;
                &::after {
                    width: 153px; } } } }
    &__img-wrap {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        overflow: hidden; }
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        @include transitionDefault; }
    &__content {
        position: absolute;
        left: 0;
        bottom: 0;
        padding: 45px 40px 20px 0;
        max-width: 470px;
        height: 208px;
        background-color: #fff; }

    &__title {
        position: relative;
        display: block;
        max-height: 86px;
        font-family: 'Cormorant';
        font-weight: 600;
        font-size: 36px;
        line-height: 40px;
        overflow: hidden;
        @include transitionDefault; }
    &__date {
        position: absolute;
        left: 0;
        bottom: 0;
        font-size: 14px;
        font-weight: 400;
        line-height: 150%;
        letter-spacing: 0.06em;
        text-transform: uppercase;
        @include transitionDefault;
        &::before, &::after {
            content: '';
            position: absolute;
            top: -36px;
            left: 0;
            width: 153px;
            height: 1px;
            background-color: #939393; }
        &::after {
            width: 0;
            height: 2px;
            background-color: $activeColor;
            @include transitionDefault; } } }

@media (max-width: 1400px) {
    .news-item {
        &__content {
            max-width: 350px;
            height: 170px;
            padding: 30px 20px 20px 0; }
        &__title {
            font-size: 26px;
            line-height: 30px; } } }
// max-width: 1400px

@media (max-width: 576px) {
    .news-item {
        &__content {
            padding: 20px 10px 20px 0;
            max-width: 274px;
            height: 120px; }
        &__title {
            max-height: 56px;
            font-size: 24px;
            line-height: 28px; }
        &__date {
            font-size: 10px;
            &::before {
                top: -10px; } } } }
// max-width: 576px
