.socials {
    display: flex;
    flex-wrap: wrap;
    &--white {
        .socials__link-icon {
            fill: #fff; } }
    &--min {
        .socials {
            &__link {
                margin-right: 25px; }
            &__link-icon {
                width: 18px;
                height: 18px; } } }
    &__link {
        display: block;
        margin-right: 36px;
        width: 20px;
        height: 20px;
        &:hover {
            .socials__link-icon {
                fill: $activeColor; } }
        &:last-child {
            margin-right: 0; } }
    &__link-icon {
        width: 100%;
        height: 100%;
        fill: $black;
        transition: 0.3s ease; } }
