.m-available {
    &__title {
        margin-bottom: 0; }
    &__row {
        margin-bottom: 45px; }


    .card-item {
        margin-bottom: 13px; } }

.m-catalog {
    &__row {
        margin-top: 112px; } }

.m-about {
    padding: 86px 0;
    margin-top: 170px;
    &__text {
        margin-bottom: 50px;
        max-width: 650px; } }

.m-inst {
    &__name {
        font-size: 24px; }
    &__row {
        margin-top: 54px; }
    &__btn {
        margin: 50px; } }

.m-news {
    &__row {
        margin-top: 112px; } }

.m-reviews {
    padding: 80px 0;
    margin-top: 150px;
    &__btn {
        margin-top: 66px; } }

.r-slider-wrap {
    position: relative;
    margin-top: 106px;
    padding: 0 90px; }

.r-slider {
    position: relative;
    &::before {
        content: '';
        width: 2px;
        height: 100%;
        background-color: $lightGray;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 10; } }

.r-slide-nav {
    position: absolute;
    top: 40%;
    transform: translateY(-50%);
    width: 70px;
    height: 20px;
    border: none;
    cursor: pointer;
    &:hover {
        .r-slide-nav__icon {
            opacity: 1; } }
    &__icon {
        width: 100%;
        height: 100%;
        fill: $black;
        opacity: 0.4; }
    &--prev {
        left: 0;
        transform: translateY(-50%) rotate(180deg); }
    &--next {
        right: 0; } }

.r-slide-dots {
    display: flex;
    margin-top: 64px;
    align-items: center;
    justify-content: center;
    .swiper-pagination-bullet {
        margin-right: 17px;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background: #494949;
        opacity: 0.35;
        &:last-child {
            margin: 0; } }
    .swiper-pagination-bullet-active {
        opacity: 1; } }


.m-form {
    padding: 110px 0 134px;
    background: url(../img/m-form-bg.jpg) no-repeat center;
    background-size: cover;
    &__textarea {
        margin-top: 6px; } }

@media (max-width: 991px) {
    .m-catalog__row {
        margin-top: 50px; }
    .m-about {
        margin-top: 70px;
        padding: 60px 0; }
    .m-reviews {
        margin-top: 35px;
        padding: 60px 0 70px; } }
// max-width: 991px

@media (max-width: 768px) {
    .m-available {
        &__row {
            display: block; }
        &__title--left {
            text-align: left; } }
    .m-about {
        padding: 60px 0 35px;
        &__socials {
            margin-top: 30px;
            justify-content: center; }
        &__text {
            margin-bottom: 40px; }
        h2 {
            text-align: left;
            &::after {
                left: 0;
                transform: none; } } }

    .r-slider-wrap {
        margin-top: 40px;
        padding: 0; }

    .r-slide-nav {
        display: none; }

    .r-slide-dots {
        margin-top: 34px; }

    .m-reviews {
        &__btn {
            margin-top: 40px; } } }
// max-width: 768px

@media (max-width: 576px) {
    .m-catalog__row {
        margin-top: 0; }

    .m-inst {
        &__name {
            margin-bottom: 12px;
            font-size: 12px;
            line-height: 15px; }
        h2 {
            padding-top: 7px;
            padding-bottom: 0;
            &::after {
                top: 0; } }
        &__btn {
            margin: 10px 0 0; } }

    .m-form {
        padding: 70px 0 75px; } }
// max-width: 576px
